import React from 'react';
import { Radio, Divider, Table, Space, Button, Tooltip, Typography } from 'antd';
import { EditOutlined, DeleteOutlined, MessageOutlined } from '@ant-design/icons';

import Breadcrumbs from '../Common/Breadcrumbs';

const { Title } = Typography;

const ProductReview = () => {
    const path = [
        'Reviews',
        'Product'
    ]

    const columns = [
        {
            title: 'Order Number',
            dataIndex: 'orderNumber',
            key: 'orderNumber',
            sorter: true,
        },
        {
            title: 'Item',
            dataIndex: 'item',
            key: 'item',
            sorter: true,
        }, {
            title: 'Product Review',
            dataIndex: 'review',
            key: 'review',
            sorter: true,
        },{
            title: 'Action',
            dataIndex: 'action',
            key: 'action',
            render: (text, record) => (
                <Space size="small">
                    <Tooltip title="Edit">
                        <Button type="success" icon={<EditOutlined />} />
                    </Tooltip>
                    <Tooltip title="Reply">
                        <Button type="success" icon={<MessageOutlined />} />
                    </Tooltip>
                    <Tooltip title="Delete">
                        <Button type="danger" icon={<DeleteOutlined />} />
                    </Tooltip>
                </Space>
            ),
        }
    ];

    const tabledata = [
        {
            key: '1',
            review: 'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor',
            item: 'Lorem ipsum ',
            orderNumber: '002120023',
        },
    ];
    return (
        <div>
            <Breadcrumbs crumb={path} />
            <div className="site-layout-background" style={{ padding: 24, minHeight: 360 }}>
                <Radio.Group size='large'>
                    <Radio.Button value="Add New">Rating</Radio.Button>
                    <Radio.Button value="Export">Content</Radio.Button>
                    <Radio.Button value="Shop Category">Status</Radio.Button>
                    <Radio.Button value="Import">Product Name</Radio.Button>
                    <Radio.Button value="View History">Order Name</Radio.Button>
                    <Radio.Button value="Name">Seller SKU</Radio.Button>
                    <Radio.Button value="Seller SKU">Shop SKU</Radio.Button>
                    <Radio.Button value="Shop SKU">Date</Radio.Button>
                    <Radio.Button value="Brand Name">Add Product SKU Code</Radio.Button>
                </Radio.Group>
                <Divider />
                <Title level={3}>Reviews</Title>
                <Table columns={columns} dataSource={tabledata} />
            </div>
        </div >
    );
};

export default ProductReview;