import React, { Fragment } from 'react';
import { Route, Switch } from 'react-router-dom';

import ProductReviewContainer from './ProductReviewContainer';
import SellerReviewContainer from './SellerReviewContainer';
import NotFoundContainer from '../Exceptions/NotFoundContainer';

const Products = ({ match }) => {
    return (
        <Fragment>
            <Switch>
                <Route exact path={`${match.url}/product`} component={ProductReviewContainer} />
                <Route exact path={`${match.url}/seller`} component={SellerReviewContainer} />
                <Route component={NotFoundContainer} />
            </Switch>

        </Fragment>
    );
};

export default Products;